.Log {
    background-color: rgb(219, 144, 31);
    width: 100%;
    height: 40px;
    position: -webkit-sticky;
    position: fixed!important;
  }
  

  .inputField {
    box-shadow:  2px 2px 2px rgba(0, 0, 0, 0.2) !important;

}

.dateField{
  width: 100% !important;
  border: none;
  outline: none;
}


  table td{
    cursor: pointer;
  }
  
  .pro-sidebar {
     /* height: 100vh!important; */
     color: #adadad;
     height: 100%;
     width: 230px;
     min-width: 230px;
     text-align: left;
     transition: width, left, right, 0.3s;
     position: relative;
     z-index: 1009;
     margin-top: 140px;
     margin-bottom: 140px!important;
  }
  .pro-sidebar-inner{
      position: fixed!important;
      width: 230px;
      min-width: 230px;
  }
  .pro-sidebar .pro-menu {
      padding-top: 10px;
      padding-bottom: 10px;
      margin-bottom: 140px;
  }
  
  .activeClass{
    position: relative;
    color: black !important;
}

.activeClass::before{
  content: '';
  width:95%;
  height: 100%;
  position: absolute;
  background-color: #f1f6f9 ;
  animation: backgroundColor 0.5s ease;
  -webkit-animation: backgroundColor 0.5s ease;
}


@keyframes backgroundColor {
    0%{
        opacity: 0;
    } 
    100%{
      opacity: 1;
    }
} 

@media (min-width:360px) and (max-width: 575px){
  .pro-sidebar {
    color: #adadad;
    height: 100%;
    width: 100vh;
    left: 0;
    text-align: left;
    position: absolute;
    z-index: 1009;
    transition: left 0.3s ease-out;
    -webkit-transition: left 0.3s ease-out;
    -moz-transition: left 0.3s ease-out;
    -ms-transition: left 0.3s ease-out;
    -o-transition: left 0.3s ease-out;
}




.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item{
  text-align: center;
  padding: 20px 0;
}
 .pro-sidebar-inner{
     position: fixed!important;
     width: 100%;
 }
 .pro-sidebar .pro-menu {
     padding-top: 10px;
     padding-bottom: 10px;
 }

}
