.Popup_form {
    width: 100%;
    height: 350px;
    padding: 30px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.Popup_details {
    display: flex;
    justify-content: space-around;
    flex-grow: 1.5;
    flex-wrap: wrap;
}

.Popup_status{
    width: 30%;
    margin: 0 auto;
}

.Popup_feedback{
    width: 100%;
    display: flex;
    justify-items: center;

}


.Popup_feedback input{
    width: 50%;
    margin: 10px auto;
    height: 50px;
    line-height: 50px;
    text-align: center;
    outline: none;
    animation: dropDown 0.2s ease-in-out;
    -webkit-animation: dropDown 0.2s ease-in-out;
    border-radius: 10px ;
    -webkit-border-radius: 10px ;
    -moz-border-radius: 10px ;
    -ms-border-radius: 10px ;
    -o-border-radius: 10px ;
}

@keyframes dropDown {
    0%{
        height:0;
    }
    100%{
        height: 50px;
    }
}


.Popup_button{
    width: 100%;
    display: flex;
    justify-content: center;
}
.Popup_button button {
    width: 20%;
    margin: 10px auto;
    text-align: center;
}