

.Home_header_div {
    width: 100%;
    height: auto;
    padding: 0 20px;
    background-color:black;
    display: flex;
    justify-content:space-between;
}

.Home_content {
    display: flex;
}

.Home_sidebar {
    margin: 0 ;
    padding: 0 ;
    background-color: black;
}
.pro-sidebar {
    margin-top: 0px !important;
}

.Home_content_section {
    width: 100%;
}

.Home_main_container {
    display: flex;
    flex-wrap: wrap;
    margin: 20px 0;
    justify-content: space-evenly;
}

.Home_sidebar_button{
    display: none;
}

.icon{
    text-align: center;
    margin: 0 auto;
    width: 50px;
}

.Home_sub_container {
    width: 30%;
    height: 200px;
    border-radius:10px;
    position: relative;
    cursor: pointer;
    transition: all 0.2s ease-out;
    box-shadow: 0 0 5px black;
    -webkit-border-radius:10px;
    -moz-border-radius:10px;
    -ms-border-radius:10px;
    -o-border-radius:10px;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    -ms-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
}
.Home_table_title h5{
    text-align: center;
    padding: 20px 0;    
}
.Home_sub_container:hover{
    box-shadow: 0 0 10px black;
}

.Home_sub_text {
    padding: 20px 0;
    text-align: center;
}

.Home_table{
    margin-top: 20px;
    font-size: 14px;
}

.Home_table_data{
    width: 100%;
    height: 55px;
}

.profileDiv{
    padding: 0 15px;
}

.profile{
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 20px auto;
}

.Home_BounceLoader{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.Bouncer{
    display: block;
    margin: 0 auto;
    border-color: red;
}

/********************************** Media for mobile ****************************************/

@media (min-width:360px) and (max-width: 575px){

.Home_main_div{
    position: relative;
}
 /*************************************** Header *******************************************/
.Home_header_div {
    position: relative;
    z-index:0;
    padding: 0;
}

.Home_header_div h4{
    font-size: 12px;
}
.Home_header_div h6{
    font-size: 12px;
}


.Home_header_div button{
    background-color: #fff;
    color: #000;
    transform: scale(0.8);
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
}

.Home_sub_text{
    padding: 10px 0;
    transform: scale(0.5);
    -webkit-transform: scale(0.5);
    -moz-transform: scale(0.5);
    -ms-transform: scale(0.5);
    -o-transform: scale(0.5);
}

.Home_sidebar_button {
    display: initial;
    width: 50px;
}

.Home_sidebar_button i{
    font-size:18px;
    color: #fff;
    margin: 15px 20px;  
}

.icon{
    width: 20px;
}
/**************************** Main Content  **********************/

.Home_sidebar{
    position: absolute;
    z-index: 2000;
    transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
}

.pro-sidebar-close{
    left : -100% ;
    opacity: 0;
   }
   
.pro-sidebar-open{
     left: 0% ;
     opacity: 1;
   }
/*************************************** Table *******************************************/
.Home_table{
    overflow-x: scroll;
    margin-top: 20px;
    font-size: 14px;
}

}