.FieldDiv{
  display: flex;  
  justify-content: space-evenly;
}

.SelectDiv{
  overflow: hidden;
  width: 205px;
}

.SelectField{   
    width:  100%;
    height: 40px;
    cursor:pointer
}


@media (min-width:360px) and (max-width: 575px){

    .FieldDiv{
        margin: 10px auto;
        padding: 0 30px !important ;
        display: flex;  
        justify-content: space-evenly;
      }
      

    .SelectDiv{
      overflow: hidden;
      width: 205px;
    }

    .SelectField{   
        width:  100%;
        height: 40px;
        cursor:pointer;
        overflow: hidden;
    }
     
    .Branch_table{
      overflow-x: scroll;
    }
    
}